.label-warning-jobactive {
  background: -webkit-gradient(linear, left top, right top, from(#05a041), to(#43c469));
  background: -webkit-gradient(linear, left top, right top, from(#05a041), to(#43c469));
  background: -webkit-gradient(linear, left top, right top, from(#05a041), to(#43c469));
  background: linear-gradient(to right, #05a041, #43c469);
}
.pcoded-badge-jobactive  {
  display: block;
  font-size: 14px;
  padding: 0 15px;
  text-decoration: none;
  position: relative;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  margin-top: -5px;
  
}

